import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Row, Card, Form, Input } from 'antd';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';



import { factorOfTrainingScore } from 'dump/data';

@Form.create({ name: 'formSpcializeTraining' })
class FormFactorOfTrainingScorePage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      specializeTrainingId: null,
      initialValue: {},
    };
    this.updateTitle ='Cập nhật tham số';
    this.addNewTitle = 'Thêm tham số';
  }

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ specializeTrainingId: match.params.id });
      this.getSpecializeTrainingById(match.params.id);
    }
  };

  getSpecializeTrainingById = id => {
    const SpecializeTrainingByID = factorOfTrainingScore.filter(specializedTraining => specializedTraining.code === id)[0];
    const initialValue = this.handleInitialValue(SpecializeTrainingByID);
    this.setState({ initialValue });
  };

  handleInitialValue = initialValue => {
    const initialValueDefault = {
      trainingIndustryCode: '',
      code: '',
      name: '',
      nameEn: '',
    };
    return { ...initialValueDefault, ...initialValue };
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { specializeTrainingId } = this.state;
    return (
      <Card title={specializeTrainingId ? this.updateTitle : this.AddNewString}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item {...formItemLayout} label="Tên quy chế" hasFeedback>
              {getFieldDecorator('name', {
                initialValue: initialValue.regulation,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Mã tham số" hasFeedback>
              {getFieldDecorator('code', {
                initialValue: initialValue.code,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên tham số" hasFeedback>
              {getFieldDecorator('name', {
                initialValue: initialValue.name,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Giá trị" hasFeedback>
              {getFieldDecorator('name', {
                initialValue: initialValue.value,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormFactorOfTrainingScorePage {...props} />
  </Page>
));



